body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.layout{margin-bottom:6rem}
.navbar{padding:1.25rem 0 1.25rem 0;background-color:#fffbfb;display:flex;position:fixed;bottom:0;align-items:flex-end;width:100%;justify-content:space-evenly}.navbar__content{display:flex;justify-content:center;flex-direction:column;align-items:center;width:3.25rem}.navbar__title{font-size:.7rem;font-family:"Jost",sans-serif;font-weight:700}
.photo{margin:0 1rem}.photo__image{width:100%}.photo__description{display:inline;padding-top:1rem;font-family:"PT Sans",sans-serif;color:#888;font-weight:400}
.orchestra-quote{background-color:#0091cf;color:#fff;height:8.37rem;display:flex;flex-direction:column;justify-content:center;padding-left:1.5rem;padding-right:1.5rem;font-family:"PT Sans",sans-serif;font-size:1.125rem}.orchestra-quote__content{font-weight:700;line-height:1.688rem;margin-top:.313rem}.orchestra-quote__author{font-weight:400;margin-top:.313rem;margin-bottom:.626rem}
.home__header{text-align:center}
.view-schedule-button{height:66px;width:80%;text-transform:uppercase;background-color:#3c3c3c;border-radius:5px;color:#fff;font-weight:700;font-family:"Poppins",sans-serif;font-size:1rem;display:flex;flex-direction:column;justify-content:center;align-items:center;text-decoration:none;margin:38px auto}
.paragraph{font-family:"PT Sans",sans-serif;font-size:1.125rem;padding:0 41px;line-height:27px}
.event-header-details{color:#fff;background-color:#fa825d;line-height:1.5rem;font-family:"PT Sans",sans-serif;padding:1.438rem}.event-header-details>*{margin:0}.event-header-details__divider{margin-left:.313rem;margin-right:.313rem}.event-header-details__venue{font-weight:bold}.event-header-details__location{font-weight:normal}
.youtube-embed{width:100%;height:16rem}
.header-image-video-container{background:linear-gradient(180deg, #fa825d 50%, #ffff 50%);padding:1rem;display:flex;justify-content:center}.header-image{border-radius:15px;max-width:100%}
.header-home{color:#fff;background-color:#fa825d;padding-left:.625rem;font-family:"Poppins",sans-serif;letter-spacing:-0.05em;padding-bottom:.625rem;font-weight:700}.header-home__title{margin-bottom:.313rem;font-size:1rem;color:#ffffc7;padding-top:.625rem;margin-bottom:"Oswald",sans-serif;font-size:1rem;line-height:111.2%}.header-home__subtitle{line-height:111.2%;text-transform:capitalize;font-size:2.25rem;margin-top:.625rem;text-align:left}
.schedule-item{display:flex;flex-direction:column;align-items:center;line-height:1.5rem;font-family:"PT Sans",sans-serif;width:100%}.schedule-item__top{font-family:"Poppins",sans-serif;display:flex;align-items:center;justify-content:space-between;align-items:baseline;width:100%;padding:.5rem}.schedule-item__subheader{color:#9c9c9c}.schedule-item__image-conatiner{display:flex}.schedule-item__content{word-wrap:break-word;width:100%}.schedule-item__hr{width:100%;color:#d3d3d3;margin:0 .5rem 0 .5rem}
.schedule{padding:2rem}
